* {
  margin: 0;
  padding: 0;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vmin;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__minutes {
  transform-origin: 50% 100%;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotator;
  position: absolute;
  top: calc(50% - 20vmin);
  left: calc(50% - 1.5vmin);
}

.stopwatch__seconds {
  transform-origin: 50% 100%;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotator;
  position: absolute;
  top: calc(50% - 38vmin);
  left: calc(50% - .75vmin);
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch--speed-up .stopwatch__seconds {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch__minutes {
  animation-duration: 600s;
}

@keyframes rotator {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(1turn);
  }
}
